function setLang(lang) {
    var b_loc = $("#b_loc");
    
    window.lang = lang;
    $.removeCookie("lang");
    $.cookie("lang", lang, {
		path: "/"
	});
    
    $("option[value='" + lang + "']", b_loc).attr("selected", "selected");
}

function initMain() {
    var win = $(window),
        body = $("body"),
        doc = $(document),
        header = $("#header"),
        w = win.width(),
        h = win.height(),
        hh = header.height(),
        fscreen = $(".js-fullscreen"),
        jhinted = $(".js-hinted-fullscreen"),
        parallax = $(".js-parallax-image"),
        jsfull = $(".section-photo-fullscreen"),
        pobjs = [],
        pobj,
        i,
        cname = "transform",
        btn_maintop = $("#btn_maintop"),
        rs,
        browser;
    
    rs = function() {
        w = win.width();
        h = win.height();
        
        doc.scrollTop(0);
        
        body.css("paddingTop", h);
        fscreen.css("height", h);
        jsfull.css("height", h);
    };
    
    win.resize(rs);
    
    rs();
    
    setLang($.cookie("lang") || window.__lang || "en_US");
    
    $("#b_loc").on("change", function(e) {
    	var val = $(this).val();
    	setLang(val);
    });
    
    // jhinted.css("height", h-hh);
    
    browser = window.bowser;
    
    if (browser.webkit || browser.safari || browser.opera)
    {
        cname = "-webkit-transform";
    }
    else if (browser.mozilla)
    {
        cname = "-moz-transform";
    }
    else if (browser.msie)
    {
        cname = "-ms-transform";
    }
    
    for (i=0; i < parallax.length; i++)
    {
        pobj = {
            html: $(parallax[i])
        };
        pobj.parent = $(pobj.html.parent());
        pobj.dataOrigin = pobj.html.attr("data-origin");
        pobjs.push(pobj);
    }
    
    btn_maintop.bind("click", function(e) {
        e.preventDefault();
        e.stopPropagation();
        
        doc.scrollTop(0);
    });
    
    $.each([
        {
            name: "btn_item1", 
            url: "mdi"
        },
        {
            name: "btn_item2",
            url: "dashboard"
        }
    ], function(i, obj) {
        $("#" + obj.name).bind("click", function(ev) {
            ev.preventDefault();
            ev.stopPropagation();

            var b_loc = $("#b_loc"),
                lang = $("option:selected", b_loc).val() || "en_US";
                
            setLang(lang);
            
            window.location.replace("./launcher/login.jsp?lang=" + window.lang + "&app=" + obj.url + "&mts=" + (window.__mts_id || "ROOT"));
        });
    });
    
    win.scroll(function(e) {
        var t = doc.scrollTop(),
            i,
            pd,
            poff,
            pval;
        
        if (t > (h - hh))
        {
            body.removeClass("nav-light");
            body.addClass("nav-dark");
        }
        else
        {
            body.removeClass("nav-dark");
            body.addClass("nav-light");
        }
        
        for (i=0; i < pobjs.length; i++)
        {
            pd = pobjs[i];
            if (pd.dataOrigin == "0")
            {
                pval = - (t / h) * 90; // (0 - t) * (t / h);
            }
            else
            {
                poff = pd.parent.offset();
                pval = (poff.top - t) / h * 90;
            }
            pd.html.css(cname, "matrix(1, 0, 0, 1, 0, " + (pval) + ")");
        }
    });
    
    // request mts
    if (window.__uri)
    {
	    var req = ig$.servlet;
	    
	    if (req.substring(0, 3) == "../")
	    {
	    	req = req.substring(1);
	    }
	    
	    $.ajax({
			type: "GET",
			url: req + "?sreq=mts&servername=" + window.__uri, 
			dataType: "json",
			timeout: 10000,
			success: function(data) {
				if (data && data.result)
				{
					window.__mts_id = data.result;
				}
			},
			error: function(e, status, thrown) {
			}
		});
    }
}

$(document).ready(function() {
    initMain();
});